const KEY = 'USER';

var currUser = {
    name: "Ochoa Hyde",
    coins: 100,
    moves: []
}

function getUser() {
    let userFromStorage = localStorage.getItem(KEY);
    if (userFromStorage !== currUser.name) {
        return currUser = {
            name: userFromStorage,
            coins: 100,
            moves: [],
        }
    }
    return currUser;
}

function signup(username) {
    localStorage.setItem(KEY, username);
}

function logout() {
    localStorage.setItem(KEY, '');
}

function addMove(contact, amount) {
    console.log('contact, amount: ', contact, amount);
    if (currUser.coins - amount >= 0) {
        let move =
        {
            toId: contact._id,
            to: contact.name,
            at: Date.now(),
            amount: amount
        }
        console.log('addMove - move: ', move);
        currUser.moves.unshift(move);
        console.log('addMove - currUser: ', currUser);
        currUser.coins -= amount;
        return 'Transaction completed';
    } else {
        return 'No sufficient funds';
    }

}
export const UserService = {
    getUser,
    signup,
    logout,
    addMove
}