import React, { Component } from 'react';
// import { ContactService } from '../services/ContactService';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import { loadContactById, saveContact, deleteContact } from '../actions/ContactActions';

import TransferFund from '../cmps/TransferFund';

class ContactDetails extends Component {
    async componentDidMount() {
        this.loadContact();
    };

    loadContact() {
        const id = this.props.match.params.id;
        this.props.loadContactById(id);
    };

    onGoBackClickHandler = (ev) => {
        this.props.history.go(-1);
    };

    deleteClickHandler = async () => {
        await this.props.deleteContact(this.props.contact._id);
        this.props.history.push('/contact');
    };

    componentDidUpdate(prevProps, prevState) {
        // for watching a change in match params to reload the contact if needed
        if (prevProps.match.params.id !== this.props.match.params.id) {
            this.loadContact();
        }
        // for handling changes without componentDidMount
    };

    componentWillUnmount() {
        // KEEP IN MIND:
        // set currContact in store to null ?
    };

    render() {
        const { contact } = this.props;

        if (!contact) {
            return <div>Loading...</div>
        }

        const imgStyle = {
            height: '100px',
        };

        return (
            <div >
                <Link to={`/contact/edit/${this.props.contact._id}`}>
                    <button>Edit</button>
                </Link>
                <button onClick={this.deleteClickHandler}>Delete</button>
                <button onClick={this.onGoBackClickHandler}>Go Back</button>
                <div className="ContactDetails brown-text container">
                    <div className="ContactDetails-main">

                        <img
                            style={imgStyle}
                            src={`https://robohash.org/${contact.name}.png`}
                            alt=""
                        />
                        <h1 >{contact.name}</h1>
                    </div>
                    <p>Phone: {contact.phone}</p>
                    <p>Email: {contact.email}</p>
                    <hr />

                    <TransferFund contact={contact} />
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        contact: state.contact.currContact,
    };
};

const mapDispatchToProps = {
    loadContactById,
    saveContact,
    deleteContact,
};

export default connect(mapStateToProps, mapDispatchToProps)(ContactDetails);
