import React, { Component } from 'react';
// import BitcoinService from '../services/BitcoinService';


//  react-google-charts

export default class StatisticPage extends Component {
    render() {
        return (
            <div className="StatisticPage">
                <h4>Market Price (USD)</h4>
                <p>Average USD market price across major bitcoin exchanges.</p>
                <hr />
                <h4>Confirmed Transactions Per Day</h4>
                <p>The number of daily confirmed Bitcoin transactions.</p>
            </div>
        )
    }
}