import React from 'react';

export default (props) => {
    console.log('move list : ', props.movesList);

    return (
        (props.movesList) ? (

            <div className="move-list">
                <h5>{props.title}</h5>

                {props.movesList.map((move) => (
                    <div className="move-list-item" key={move.at}>
                        <p>To: {move.to}</p>
                        <p>At: {move.at}</p>
                        <p>Amount: {move.amount} coins</p>
                        {/* <pre>{move}</pre> */}
                    </div>
                    // <Link to={'contact/' + contact._id} key={contact._id}>
                    // <ContactPreview contact={contact} />
                    // </Link>
                ))}
            </div>
        ) : (
                <h5>{props.title}</h5>
            )
    );
}
