import React, { Component } from 'react';
import { UserService } from '../services/UserService';

export default class TransferFund extends Component {
    state = {
        contact: null,
        amount: 0,
        msg: ''
    };

    constructor(props) {
        super(props);
        this.state.contact = { ...props };
    };

    handleChange = (ev) => {
        const { name, value } = ev.target;
        this.setState({ [name]: value });
    };

    transferFunds = (ev) => {
        ev.preventDefault();
        const { contact, amount } = this.state;
        console.log('transferFunds - contact: ', contact);

        const msg = UserService.addMove(contact, amount);
        // console.log(msg);
        this.setState({ msg })
    };

    render() {
        return (
            <div>
                <h5>Transfer coins to {this.props.contact.name}: </h5>
                <form onSubmit={this.transferFunds}>
                    <label>
                        Amount:
                        <input type="number"
                            // onChange={this.props.onTransferFund}
                            onChange={this.handleChange}
                            name="amount"
                            value={this.state.amount}
                        />
                        <button>Transfer</button>
                    </label>
                    <p>{this.state.msg}</p>
                </form>
            </div>
        )
    }
}

