import React, { Component } from 'react';
import { connect } from 'react-redux';
import { loadContactById, saveContact } from '../actions/ContactActions';
import { ContactService } from '../services/ContactService';

class ContactEdit extends Component {
    state = {
        contact: ContactService.getEmptyContact()
    };

    async componentDidMount() {
        const { id } = this.props.match.params;
        if (id) {
            await this.props.loadContactById(id);
            this.setState({ ...this.props.contact }, () => {
                console.log('this.state', this.state);
            });
        }
    };

    handleChange = (ev) => {
        const { value, name } = ev.target;
        this.setState({ [name]: value });
    };

    saveContact = async (ev) => {
        ev.preventDefault();
        const contact = await this.props.saveContact({ ...this.state });
        this.props.history.push(`/contact/${contact._id}`);
    }

    render() {
        const { name, email, phone } = this.state;
        return (
            <div>
                <form onSubmit={this.saveContact}>
                    <div>
                        <label htmlFor="">
                            Name:
                            <input type="text"
                                onChange={this.handleChange}
                                value={name}
                                name="name"
                            />
                        </label>
                    </div>
                    <div>
                        <label htmlFor="">
                            Email:
                            <input type="text"
                                onChange={this.handleChange}
                                value={email}
                                name="email"
                            />
                        </label>
                    </div>
                    <div>
                        <label htmlFor="">
                            Phone:
                            <input type="text"
                                onChange={this.handleChange}
                                value={phone}
                                name="phone"
                            />
                        </label>
                    </div>
                    <div>
                        <button>SAVE</button>
                    </div>
                </form>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    contact: state.contact.currContact,
});

const mapDispatchToProps = {
    loadContactById,
    saveContact
};

export default connect(mapStateToProps, mapDispatchToProps)(ContactEdit);