import React from 'react';

export default ({ contact }) => {
    const imgStyle = {
        height: '40px',
    };

    return (
        <div className="ContactPreview brown-text" key={contact._id}>
            <img
                style={imgStyle}
                src={`https://robohash.org/${contact.name}.png`}
                alt=""
            />
            <h4>

                {contact.name}
            </h4>
        </div>
    );
}
