import React, { Component } from 'react'
import { UserService } from '../services/UserService';
import { BitcoinService } from '../services/BitcoinService';
import MoveList from '../cmps/MoveList';

export default class HomePage extends Component {
    state = {
        user: null,
        rate: null,
        title: ''
    };

    async componentDidMount() {
        const results = await Promise.all([UserService.getUser(), BitcoinService.getRate()]);
        const [user, rate] = results;
        if (!user.name) {
            this.props.history.push('/signup');
        }
        // const user = await UserService.getUser();
        // const rate = await BitcoinService.getRate();
        this.setState({ user, rate });
        this.setTitle();
    };

    setTitle() {
        if (this.state.user.moves.length) {
            this.setState({ title: 'Your last 3 moves:' })
        } else {
            this.setState({ title: 'You have no moves at the moment' })

        }
    }
    get avatarImg() {
        return `https://robohash.org/${this.state.user.name}.png`
    };

    handleLogout = () => {
        UserService.logout();
        this.props.history.push('/signup');

    }
    render() {
        return (
            (this.state.user) ?
                (
                    <div className="user-msg container brown-text">
                        <h2>{this.state.user.name}, Welcome back!</h2>
                        <img
                            // style="height: '50px'"
                            src={this.avatarImg}
                            alt=""
                        />
                        <h5>you have {this.state.user.coins} coins.</h5>
                        <h5>Current coin rate: {this.state.rate}</h5>
                        <hr />
                        <MoveList title={this.state.title} movesList={this.state.user.moves} />

                        <button onClick={this.handleLogout}>Logout</button>
                    </div>
                )
                : (<div>Please login first...</div>)
        )
    }
}